import { useStaticQuery, graphql } from 'gatsby';
import React, { useState } from 'react';
import './ProjectBox.css';

interface Props {
    height: number;
    imageSrc: string;
    boxText: string;
}


export const ProjectBox = (props: Props) => {
	const [ render, setRender ] = useState(false);

    function renderText() { setRender(true); }
    function hideText() { setRender(false); }

	/*const { backgroundImage } = useStaticQuery(
		graphql`
		  query {
			backgroundImage: file(relativePath: { eq: "mt_elbert.jpg" }) {
			  childImageSharp {
				gatsbyImageData(
				  height: 800
				  quality: 100
				  placeholder: BLURRED
				  formats: [AUTO, WEBP, AVIF]
				)
			  }
			}
		  }
		`
	)*/

    
	return(
		<div
			className="ProjectBox" 
			style={{
				background: `url(${props.imageSrc}) 50% 50% no-repeat`,
				backgroundSize: 'cover',
				width: '100%',
				height: props.height,
				cursor: 'pointer'
			}}
			onMouseEnter={renderText}
			onMouseLeave={hideText}
		>
			{
				render &&
				<div style={{backgroundColor: '#212121', padding: 10, color: 'white'}}>
					{props.boxText}
				</div>
			}
		</div>
	)
    
}