import { Grid } from "@material-ui/core";
import React from "react";
import Layout from "../../components/layout/Layout";
import { ProjectBox } from "../../components/projectboxes/ProjectBox";
import goTetris from '../../images/GoTetris.png';

export default function GoTetris() {
    return(
		<Layout>
			<Grid container spacing={2}>
				<Grid item xs={5}>
					<div className="box" style={{padding: 20}}>
						<div className="titleText secondary">Game of Thrones Tetris</div>
					</div>
					<div style={{padding: 20, color: 'white'}}>
						There was a time long ago when Game of Thrones was an iconic television show that inspired millions.  Not so much today.
						Sometime circa 2016 the gif to the right started circulating around the internet.  Annoyed no-one actually made this into reality,
						I pulled some tetris code online and reworked the imagery and mechanics.  The gif is now a playable web function.
						I wish the show held up as well as this code has.
					</div>
					<div style={{padding: 20, color: 'white'}}>
						It's not that I necessarily hated the ending of the show (though Bran "I can't be king" yet a few episodes later "why do you think I came
						all this way" bitch ass makes no sense), rather the writers of the show didn't earn this ending.  It was rushed and lazy and there is no reason
						we couldn't have had 3 more episodes.  Also the penultimate battle of the show was too dark to see anything.  Also if you calculate the 
						velocity required of that ship's harpoon to hit a dragon from that range while parallel to the water's surface, the thrust required to launch it
						is equivalent to that of a Saturn V rocket.  Also Dany how can you not see the boats you're flying above the ocean on a dragon you dolt.
						Such fucking lazy writing.  Also how they wrote Varys out of the show was a disgrace to his character and the actor.
					</div>
					<div style={{padding: 20, color: 'white'}}>
						Original vanilla JS code can be found <a href="https://github.com/Mistweaver/GameOfThronesTetris">here.</a>  Currently working on a React version of this code.
					</div>
				</Grid>
				<Grid item xs={7}>
					<video /*width="320"*/ /*height="240"*/ controls autoPlay={true} style={{marginTop: 10}}>
						<source src="https://thumbs.gfycat.com/JampackedDetailedAssassinbug-mobile.mp4" type="video/mp4" />
						Your browser does not support the video tag.
					</video>

					<div style={{marginTop: 25}}>
						<ProjectBox height={400} imageSrc={goTetris} boxText="My version of the GIF" />
					</div>
				</Grid>
			</Grid>
		</Layout>
    )
}